/* Estilos do mapa Leaflet */
#map.custom-popup {
    position: fixed;
    top: 55px;
    left: 0;
    right: 0;
    bottom: 0;
}

.row {
    margin-bottom: 0;
}

.leaflet-container {
    overflow: inherit !important;
}

.ocorrencias-mapa-fixa-container {
    padding: 20px;
    margin-top: 70px;
    transition: margin-left 0.3s ease;
    border-radius: 8px;
    background-color: #f9faff;
}

.ocorrencias-mapa-fixa-container.closed {
    margin-left: 70px;
}

.ocorrencias-mapa-fixa-container.open {
    margin-left: 238px;
}

.close {
    color: #aaa;
    position: absolute;
    top: 10px;
    right: 10px;
    font-size: 28px;
    cursor: pointer;
}

#map {
    transition: margin-left 0.3s ease;
    /* position: absolute; */
    top: 0;
    left: 0;
    
}



.ocorrencias-mapa-fixa-container.open #map {
    margin-left: 238px;
    
}

.ocorrencias-mapa-fixa-container.closed #map {
    margin-left: 70px;
}

div.leaflet-control-layers.leaflet-control.leaflet-control-layers-expanded section.leaflet-control-layers-list div label span {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    padding: 0 !important;
    margin: 0 !important;
    min-width: 0 !important;
    max-width: none !important;
    width: auto !important;
}

.leaflet-control-layers-selector {
    margin: 5px !important;
    padding: 0 !important;
    min-width: 0 !important;
    max-width: none !important;
    width: auto !important;
}

/* Estilos do popup personalizado */
.custom-popup .leaflet-popup-content-wrapper {
    background: #2c3e50;
    color: #fff;
    font-size: 16px;
    line-height: 24px;
}

.custom-popup .leaflet-popup-content-wrapper a {
    color: rgba(255, 255, 255, 0.5);
}

.custom-popup .leaflet-popup-tip-container {
    width: 30px;
    height: 15px;
}

.custom-popup .leaflet-popup-tip {
    border-left: 15px solid transparent;
    border-right: 15px solid transparent;
    border-top: 15px solid #2c3e50;
    margin-bottom: 0;
    background: #2c3e50;
}

/* Estilos do card lateral de informações do marcador */
#infoCard {
    position: absolute;
    top: 150px;
    right: 20px;
    background-color: #fff;
    padding: 10px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    border-radius: 5px;
}

#tituloInfo {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 5px;
}

#codigoTqa {
    font-size: 14px;
    color: #666;
}

#rotaIda {
    font-size: 14px;
    color: #666;
}

#fecharInfo {
    margin-top: 10px;
}

.mapa-fixa-container {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
